import React, { useState } from 'react';
import styles from './css/Main.module.css';
import { TrendUnit, ShowBtn } from './TrendUnit';

const Trend = (props) => {
  const [trendCount, setTrendCount] = useState(4);
  const [showClose, setShowClose] = useState(false);

  const handleShowMore = () => {
    if (showClose) {
      setTrendCount(4); // 'Close'   trendCount  4 
    } else {
      setTrendCount(trendCount + 3); // 'Show More'   trendCount 3 
    }
    //   showClose state 
    setShowClose(!showClose);
  };

  const trendUnits = [
    { title: '  2024', count: '5,123 Post' },
    { title: ' ', count: '1,915 Post' },
    { title: '   ', count: '513 Post' },
    { title: '  ', count: '1,645 Post' },
    { title: ' ', count: '7,777 Post' },
    { title: ' ', count: '6,458 Post' },
    { title: ' :  ', count: '3,946 Post' },
  ];
  return (
    <div className={styles.TrendContainer}>
      <div className={styles.TrendTitle}>ReadWe Trend for you</div>
      <div className={styles.TrendUnits}>
        {/*    TrendUnit . */}
        {trendUnits.slice(0, trendCount).map((unit, index) => (
          <TrendUnit key={index} textTitle={unit.title} textCnt={unit.count} />
        ))}
        {/* ShowBtn   handleShowMore  . */}
        <ShowBtn onClick={handleShowMore}>
          {showClose ? 'Close' : 'Show'}
        </ShowBtn>
      </div>
    </div>
  );
};

export default Trend;
