import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import styles from './css/myPage.module.css';
import '../myPage/ProfileEdit.css';

const ProfileEdit = () => {
  const location = useLocation();
  const token = localStorage.getItem('token');
  const [nickname, setNickname] = useState(location.state.beforeNickname);
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [isPasswordMatching, setIsPasswordMatching] = useState(false);

  const [imgSrc, setImgSrc] = useState(null);
  const [preview, setPreview] = useState(location.state.beforeImg);
  const [id, setId] = useState('account_test93');

  const [uploadedFileName, setUploadedFileName] = useState(null);

  // 
  const handleNicknameChange = (event) => {
    if (event.target.value.length > 20) {
      return;
    }
    setNickname(event.target.value);
  };

  // 
  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };
  //  
  const handleConfirmPasswordChange = (event) => {
    setConfirmPassword(event.target.value);
  };
  //     
  useEffect(() => {
    setIsPasswordMatching(password === confirmPassword && password !== '');
  }, [password, confirmPassword]);

  // const handleFileChange = (event) => {
  //   const file = event.target.files[0];
  //   setSelectedFile(file);
  //   console.log(file);
  //   setPreview(URL.createObjectURL(file));
  // };

  // const handleUpload = async () => {
  //   console.log(id);
  //   const formData = new FormData();
  //   formData.append('file', selectedFile);
  //   formData.append('id', id);

  //   const url = 'https://api.mandarin.weniv.co.kr';
  //   const fileNames = [];

  //   try {
  //     const response = await fetch(url + '/image/uploadfiles', {
  //       method: 'POST',
  //       body: formData,
  //     });

  //     const data = await response.json();

  //     for (let i of data) {
  //       fileNames.push(i.filename);
  //     }

  //     if (fileNames.length > 1) {
  //       return fileNames.join(',');
  //     } else {
  //       return fileNames[0];
  //     }
  //   } catch (err) {
  //     console.error(err);
  //   }

  //   return true;
  // };

  //  API_
  const uploadImage = async (imgFile) => {
    //  
    const baseUrl = 'https://api.mandarin.weniv.co.kr/';
    const reqUrl = baseUrl + 'image/uploadfile';

    // (.append('',))
    const form = new FormData();
    form.append('image', imgFile);

    const res = await fetch(reqUrl, {
      method: 'POST',
      body: form,
    });
    const json = await res.json();
    const imgUrl = baseUrl + json.filename;
    setImgSrc(imgUrl);
    setPreview(imgUrl);
  };
  // API 
  const onChangeImg = (e) => {
    console.log(e.target.files[0]);
    const imgFile = e.target.files[0];
    uploadImage(imgFile);
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (password !== confirmPassword) {
      // 
      alert('  .');
      return;
    }
    fetch('https://api.mandarin.weniv.co.kr/user', {
      method: 'PUT',
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        user: {
          accountname: location.state.id,
          username: nickname,
          image: imgSrc,
        },
      }),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.user) {
          alert('   ');
          window.location.href = '/main'; //   
        } else {
          alert('   ID.');
        }
      })
      .catch((error) => {
        console.error('Error:', error);
        alert('  .  .');
      });
  };

  return (
    <div className={styles.pageWrap}>
      <div className={styles.contentArea}>
        <h1 className={styles.pageTitle}> </h1>
        {/*     +   */}
        <div className={styles['imgChange-area']}>
          <div className={styles['img-box']}>
            <img alt=" " src={preview} />
          </div>
          <div className={styles['imgChange-area-input']}>
            <input
              className={styles['imgChange-input']}
              type="file"
              name="image"
              accept="image/*"
              onChange={onChangeImg}
            />
          </div>
        </div>

        {/*    */}
        <input
          type="text"
          placeholder=""
          className="basic gray"
          value={nickname}
          onChange={handleNicknameChange}
          style={{ marginBottom: '10px' }}
        />
        <p>  ,  .</p>
        <p> 20  </p>

        {/* 1    */}
        <input
          type="password"
          placeholder=""
          className="basic gray"
          onChange={handlePasswordChange}
          style={{ marginTop: '30px' }}
        />

        {/* 2    */}
        <input
          type="password"
          placeholder=" "
          className="basic gray"
          value={confirmPassword} // 
          onChange={handleConfirmPasswordChange}
          style={{ marginTop: '20px' }}
        />

        {isPasswordMatching && <p> .</p>}

        <button
          onClick={handleSubmit}
          style={{
            backgroundColor: '#E87C3E',
            border: 'none',
            color: 'white',
            padding: '15px 32px',
            textAlign: 'center',
            textDecoration: 'none',
            display: 'block',
            fontSize: '16px',
            margin: '50px auto',
            borderRadius: '10px',
            cursor: 'pointer',
          }}
        >
          
        </button>
      </div>
    </div>
  );
};

export default ProfileEdit;
