import React, { useState, useEffect } from 'react';
import './Login.css';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';

export default function LoginPage() {
  // input  
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  //   
  const inputEmail = (e) => {
    setEmail(e.target.value);
  };
  const inputPassword = (e) => {
    setPassword(e.target.value);
  };

  // const inputEmail = (e) => {
  //   setPassword(e.target.value);
  // };

  //  
  const submitLogin = (e) => {
    //   
    e.preventDefault();
    loginFn(email, password);
  };

  const loginFn = async (email, password) => {
    const reqUrl = 'https://api.mandarin.weniv.co.kr';
    const reqPath = '/user/login';
    const loginData = {
      user: {
        email: email,
        password: password,
      },
    };
    // fetch post 
    try {
      const res = await fetch(reqUrl + reqPath, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(loginData),
      });

      if (res.ok) {
        //   -   
        const json = await res.json();
        console.log(json);
        const token = json.user.token;
        console.log(token);

        const email = json.user.email;

        //      
        localStorage.setItem('token', token);
        localStorage.setItem('email', email);
        console.log(' !     :', token);

        navigate('/main', { state: { email } });
      }
      //  
      else {
        console.error(' :', res.status, res.statusText);
        alert(' .     .');
      }
    } catch (error) {
      // 
      console.error('  :', error);
      alert('   .');
    }
  };

  return (
    <>
      <div className="login-container">
        <div className="login-Image"></div>
        <form onSubmit={submitLogin}>
          <label></label>
          <input
            className="input-box id"
            type="text"
            placeholder=""
            value={email}
            onChange={inputEmail}
            required
          />
          <br />

          <label></label>
          <input
            className="input-box pw"
            placeholder=""
            type="password"
            value={password}
            onChange={inputPassword}
            required
          />
          <br />

          <input type="checkbox" id="check_btn" />
          <label for="check_btn">
            <span>   </span>
          </label>
          <button className="Loginbutton" type="submit">
            
          </button>
        </form>
        <p className="Find-Id">  |  </p>
        <div className="joinment-box">
            ?
          <Link to="/join" className="logintojoin">
            
          </Link>
          {/*    */}
        </div>
        <div className="line-container">
          <span className="login-line"></span>
        </div>
        <p className="account-box toemail">    </p>
        <div className="account-box email-login">
          <Link className="email-image" to="/email-signup"></Link>
          <Link className="naver-image" to="/email-signup"></Link>
          <Link className="kakao-image" to="/email-signup"></Link>
          <Link className="icon1-image" to="/email-signup"></Link>
        </div>
        <div className="line-container">
          <span className="ment-1">
            * SNS      .
          </span>
        </div>
      </div>
      <div className="Footer" style={{ marginTop: '50%' }}></div>
    </>
  );
}
