import React, { useState } from 'react';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import SuccessPopup from './SuccessPopup'; //  
import './JoinPage.css';
import { useNavigate } from 'react-router-dom';

export default function JoinPage() {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [accountname, setAccountname] = useState('');
  const [password, setPassword] = useState('');
  const [imgSrc, serImgsrc] = useState(
    'http://api.mandarin.weniv.co.kr/Ellipse.png',
  );

  const navigate = useNavigate();

  //  
  const join = async (joinData) => {
    const reqUrl = 'https://api.mandarin.weniv.co.kr/user';
    const res = await fetch(reqUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(joinData),
    });
    const json = await res.json();
    console.log(json);

    // const token = json.user.token;
    // console.log(token);

    // localStorage.setItem('token', token);

    if (res.status === 200) {
      //      
      alert(' .');

      //    /join-success 
      navigate('/join-success', { state: { username } }); // username state 
    } else {
      //      
      alert(' .   .');
    }
  };

  const inputUsername = (e) => {
    setUsername(e.target.value);
  };
  const inputEmail = (e) => {
    setEmail(e.target.value);
  };
  const inputPassword = (e) => {
    setPassword(e.target.value);
  };

  const inputAccountname = (e) => {
    setAccountname(e.target.value);
  };

  const uploadImage = async (imageFile) => {
    const baseUrl = 'https://api.mandarin.weniv.co.kr/';
    const reqUrl = baseUrl + 'image/uploadfile';
    // 
    const form = new FormData();
    //  ("","")
    //  
    form.append('image', imageFile);
    //  
    const res = await fetch(reqUrl, {
      method: 'POST',
      body: form,
    });
    const json = await res.json();
    console.log(baseUrl + json.filename);
    const imageUrl = baseUrl + json.filename;
    serImgsrc(imageUrl);
  };

  const onChangeImage = (e) => {
    // 
    const imageFile = e.target.files[0];
    //  
    // const form = new FormData();
    //  ("","")
    //  
    // form.append('image', file);
    uploadImage(imageFile);
  };
  const submitJoin = (e) => {
    const joinData = {
      user: {
        username: username,
        email: email,
        password: password,
        accountname: accountname,
      },
    };
    join(joinData);
  };

  const isCompleted = (field) => {
    switch (field) {
      case 'accountname':
        return !!accountname;
      case 'email':
        return !!email;
      case 'username':
        return !!username;
      case 'password':
        return !!password;
      default:
        return false;
    }
  };

  return (
    <>
      <section>
        <div className="join-Box">
          <h1> </h1>
          <h3 className="join-ment">
                !
          </h3>
          <div className="img-upload">
            <label>
              <img src={imgSrc} alt="" id="imagePre" />
            </label>
            <input
              type="file"
              id="profileImg"
              name="image"
              accept="image/*"
              onChange={onChangeImage}
            />
          </div>
          <div
            className={`input-icon 
            ${isCompleted('email') ? 'active-input' : ''}`}
          >
            <i className="icon icon-email-w"></i>
            <input
              type="email"
              placeholder=""
              className="basic gray"
              value={email}
              onChange={inputEmail}
            />
          </div>
          <div className="input-icon join-PW">
            <i className="icon icon-lock-w"></i>
            <input
              type="password"
              placeholder=""
              className="basic gray"
              value={password}
              onChange={inputPassword}
            />
          </div>
          <div
            className={`input-icon 
            ${isCompleted('accountname') ? 'active-input' : ''}`}
          >
            <i className="icon icon-user-w"></i>
            <input
              type="text"
              placeholder=""
              className="basic gray"
              value={accountname}
              onChange={inputAccountname}
            />
          </div>
          <div
            className={`input-icon 
            ${isCompleted('username') ? 'active-input' : ''}`}
          >
            <i className="icon icon-user-w"></i>
            <input
              type="text"
              placeholder=" "
              className="basic gray"
              value={username}
              onChange={inputUsername}
            />
          </div>
          <button type="submit" className="join-basic" onClick={submitJoin}>
            
          </button>
        </div>
      </section>
    </>
  );
}
