import React, { useEffect } from 'react';
import { BasicBtn } from '../button/BtnStyle';

//      
const BookDetailModal = (props) => {
  useEffect(() => {
    document.body.style.cssText = `
      position: fixed; 
      top: -${window.scrollY}px;
      overflow-y: scroll;
      width: 100%;`;
    return () => {
      const scrollY = document.body.style.top;
      document.body.style.cssText = '';
      window.scrollTo(0, parseInt(scrollY || '0', 10) * -1);
    };
  }, []);
  const closePopup = () => {
    props.closePopup();
  };
  return (
    <div className="modal-area">
      <div className="modal-bg"></div>
      <div className="modal-wrap">
        <h3 className="modal-title">  </h3>
        <div className="modal-content row-2">
          <div className="row-2">
            <div className="book-info-obj">
              <img
                alt=" "
                src={process.env.PUBLIC_URL + `/images/${props.bookImgSrc}`}
              />
              <BasicBtn md="true" linestyle="true" round="100px" wid="100%">
                <i className="icon icon-star" />  
              </BasicBtn>
              {/*     */}
              {/* <button
                type="button"
                className="basic w-100 round-full line md"
              >
                <i className="icon icon-star-active"></i>  
              </button> */}
            </div>
            <div className="book-info-text">
              <strong className="book-public">
                <span> |</span> {props.public}
              </strong>
              <h3 className="book-title">{props.title}</h3>
              <div>
                <span className="tag orange"> / </span>
                <strong className="book-author">{props.author}</strong>
              </div>
              <p className="book-content scrollArea">{props.description}</p>
            </div>
          </div>
        </div>
        <div className="modal-footer">
          <div className="row-2">
            <p className="notice-text">
                []   !
            </p>
            <BasicBtn sm="true" round="100px">
              
            </BasicBtn>
          </div>
        </div>
        <BasicBtn
          round="0px"
          bgcolor="#E87C3E"
          shadow="none"
          wid="100%"
          onClick={closePopup}
        >
          
        </BasicBtn>
      </div>
    </div>
  );
};

export default BookDetailModal;
